	}
	
	.add-update-preview /deep/ .el-upload .el-icon-plus {
	  border: 1px solid #FFB74D;
	  cursor: pointer;
	  border-radius: 6px;
	  box-shadow: inset 0px 0px 48px 0px #FFF5E6;
	  color: #FF4D4D;
	  width: 200px;
	  font-size: 32px;
	  line-height: 120px;
	  text-align: center;
	  height: auto;
	}
	
	.add-update-preview .el-textarea /deep/ .el-textarea__inner {
	  border: 1px solid #FFB74D;
	  border-radius: 4px;
